.homepageMainDiv{
    font-family: sans-serif;
    display: grid;
    grid-template-columns: 100%;
    width: 100%;
    margin-top: 20%;
}

.animate1{
    /* animation: fadeInUp; referring directly to the animation's @keyframe declaration */
    /* animation-duration: 2s;   */
    margin-bottom: 20%;
}

.my_work_p{
    font-size: 25px;
    margin-top: 25px;
    margin-left: 15px;
}

.home_title{
    font-size: 50px;
    margin-top: 25px;
    margin-left: 20px;
    font-weight: bold;
    
}

.home_txt1{
    padding-left: 20px;
    margin-right: 20px;
    margin-top: 15px;
    background: -webkit-linear-gradient(rgb(255, 174, 0), rgb(0, 0, 0));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
   

}

.txtDiv1{
    margin-top: -50px;
    grid-column: 1/-1;
}

.home_img1{
    grid-column: 1;
    text-align: end;
    margin-left: -40px;
    margin-top: -10px;

}

.home_btn1{
    margin-top: 15px;
    width: 200px;
    margin-left: 25px;
    color: black;
    background: -webkit-linear-gradient(rgb(255, 174, 0), rgb(255, 230, 0));
    border: 0px;
    padding: 15px;
    border-radius: 25px;
    font-weight: bold;

}

.home_txt2{
    text-align: center;
    padding-top: 55px;
    padding-bottom: 25px;
    font-size: 30px;
    background: -webkit-linear-gradient(rgb(255, 174, 0), rgb(0, 0, 0));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}