.about_mainDiv{
    display: grid;
    grid-template-columns: 100%;
    margin-top: 25px;
}

.about_div1{
    padding-bottom: 25px;
    background-color:rgb(255, 174, 0);
    grid-column: 1;
    padding-top: 15px;
    text-align: center;
}

.about_txt1{
    font-size: 25px;
    color: white;
    padding-bottom: 15px;
}

.about_txt2{
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 15px;
}

.about_txt3{
    margin: auto;
    padding-left: 25px;
    text-align: center;
    padding-right: 25px;
    padding-top: 5px;
}

.about_mainDiv  span{
    text-align: center;
}