.f_mainDiv{
    display: grid;
    grid-template-columns: 50% 50%;
    /* grid-template-rows: 250px; */
    grid-column-gap: 20px;
    grid-row-gap: 20px;
    width: 90%;
    margin: auto;
    margin-bottom: 25px;
    justify-content: center;
}

.f_div1{
    border-radius: 15px 15px 15px 15px;
    box-shadow: 0px 0px 5px;
    grid-column: 1;
    background-color: rgb(255, 255, 255);
}

.f_div2{
    border-radius: 15px 15px 15px 15px;
    box-shadow: 0px 0px 5px;
    grid-column: 2;
    background-color: rgb(255, 255, 255);
}

.f_img{
    border-radius: 15px 15px 0px 0px;
    width: 100%;
    height: 200px;
    object-fit: cover;
}

.f_title{
    
    padding-top: 5px;
    padding-left: 10px;
    padding-right: 10px;
    text-align: center;
}

.f_desc{
    background: -webkit-linear-gradient(rgb(255, 174, 0), rgb(0, 0, 0));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-align: center;
    padding-bottom: 10px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
}