.card1_mainDiv{
    box-shadow: 0px 0px 10px;
    display: grid;
    margin: auto;
    width: 90%;
    border-radius: 15px;
   grid-template-columns: 100%;
    margin-bottom: 35px;
    text-align: center;
    
}

.card1_img{
    border-radius: 15px 15px 0px 0px ;
    height: 350px;
    width: 100%;
    object-fit: cover;
}

.card1_title{
    padding-left: 10px;
    font-weight: bold;
    padding-top: 10px;
}

.card1_description{
    padding-top: 2px;
    padding-left: 10px;
    padding-bottom: 10px;
}