.navBardviv{
    padding-top: 10px;
 
    padding-bottom: 10px;
    color: black;
    display: grid;
    grid-template-columns: 80% 20%;
    position: sticky;
    top: 0;
    box-shadow: 0px 0px 10px;
    background-color: white;
    z-index: 100;
}

.nav_call{
    color: green;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: auto;
    margin-right: 10px;
    text-align: end;
  
}

.nav_title{
    padding-bottom: 5px;
    text-align: start;
    margin-left: 15px;
    background: -webkit-linear-gradient(rgb(255, 174, 0), rgb(0, 0, 0));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: bold;
    font-size: 25px;
}